const loadScript = (src, props) => new Promise((resolve, reject) => {
  const script = document.createElement("script");
  script.src = src;
  if (Array.from(document.scripts).some(({ src: src2 }) => script.src === src2)) {
    return resolve(void 0);
  }
  Object.assign(script, props);
  script.onload = resolve;
  script.onerror = (event, source, lineno, colno, error) => {
    if (error) {
      reject(error);
      return;
    }
    if (typeof event === "string") {
      reject(new Error(`Error loading script: src: ${src} event: ${event}`));
      return;
    }
    if (event instanceof Event) {
      const target = event.target;
      const targetSrc = target.getAttribute("src") ?? "";
      reject(
        new Error(
          `Error loading script: src: ${src} targetSrc: ${targetSrc}`
        )
      );
      return;
    }
    reject(new Error(`Error loading script: src: ${src}`));
  };
  const ref = document.scripts[0];
  ref?.parentNode?.insertBefore(script, ref);
});

export { loadScript };
